import React from "react";
import BaseNavLink from "../BaseNavLink";
import { AiOutlineClockCircle, AiOutlineShopping } from "react-icons/ai";
import { FiUsers } from "react-icons/fi";
import { FaRegMoneyBillAlt } from "react-icons/fa";

import "./GuestNavPanel.scss";

// TODO: Change routes to the respective paths when screens are implemented
const GuestNavPanel = () => {
  return (
    <div className="nav-panel"> 
      <div className="guest-nav-panel">
        <div className="panel-item">
          <BaseNavLink Icon={<AiOutlineClockCircle size={20} />} to="/">
            Nation Timeline
          </BaseNavLink>
        </div>
        <div className="panel-item">
          <BaseNavLink Icon={<FiUsers size={20} />} to="/">
            Our Members
          </BaseNavLink>
        </div>
        <div className="panel-item">
          <BaseNavLink Icon={<FaRegMoneyBillAlt size={20} />} to="/">
            Entrepreneurship
          </BaseNavLink>
        </div>
        <div className="panel-item">
          <BaseNavLink Icon={<AiOutlineShopping size={20} />} to="/">
            Store
          </BaseNavLink>
        </div>
      </div>
    </div>
  );
};

export default GuestNavPanel;
