import React, { useState } from "react";
import { Input, Select, Checkbox, Radio, ResetButton, Form } from "formik-antd";
import { Formik } from "formik";
import { Row, Col, Alert } from 'antd';
import { EllipsisOutlined } from "@ant-design/icons";
import * as Yup from "yup";
import axios from "axios";
import URI from "../../Utils/utils.urls";

import "./Signup.scss";
import BackgroundImage from "../../Resources/Images/background-img.jpg";
import LogoImage from "../../Resources/Images/logo.png";
import GoogleImage from "../../Resources/Images/google-logo.png";
import FacebookImage from "../../Resources/Images/fb-logo.png";

const GenreRange = ['Blues', 'Hip-Hop/Rap', 'R&B/Soul', 'Classical', 'Jazz', 'Custom', 'Country', 'Pop'];
const ArtistDescriptions = ['Rapper', 'Producer', 'Engineer', 'Singer', 'Ghost Writer', 'Song Writer' , 'Guitarist' ,
    'Composer', 'Custom'];

const SignupSchema = Yup.object().shape({
  firstName: Yup.string()
      .min(2, "Too Short!")
      .max(50, "Too Long!")
      .required("First name is required"),
  lastName: Yup.string()
      .min(2, "Too Short!")
      .max(50, "Too Long!")
      .required("Last name is required"),
  birthMonth: Yup.string().required("Required"),
  birthDate: Yup.string().required("Required"),
  birthYear: Yup.string().required("Required"),
  email: Yup.string()
      .email("Email address is invalid")
      .required("Email is required"),
  password: Yup.string()
      .min(6, "Too Short!")
      .max(20, "Too Long!")
      .required("Password is required"),
  passwordAgain: Yup.string()
      .oneOf([Yup.ref('password'), null], 'Passwords do not match')
      .required("Password confirmation is required"),
  city: Yup.string()
      .min(1, "Too Short!")
      .max(50, "Too Long!")
      .required("City is required"),
  state: Yup.string().required("State is required"),
  zip: Yup.string()
      .min(1, "Too Short!")
      .max(6, "Too Long!")
      .required("ZIP code is required"),
  accountType: Yup.string().required("Select an account type"),
  bio: Yup.string()
      .when("accountType", {
        is: "Artist Account",
        then: Yup.string().required("Bio is required"),
      }),
  artistDescription: Yup.string()
      .when('accountType', {
          is: 'Artist Account',
          then: Yup.string().required('Description is required'),
      }),
  genre: Yup.array()
      .when("accountType", {
        is: "Artist Account",
        then: Yup.array().required("Select a genre range"),
      }),
});

const States = [
  "AK - Alaska",
  "AL - Alabama",
  "AR - Arkansas",
  "AS - American Samoa",
  "AZ - Arizona",
  "CA - California",
  "CO - Colorado",
  "CT - Connecticut",
  "DC - District of Columbia",
  "DE - Delaware",
  "FL - Florida",
  "GA - Georgia",
  "GU - Guam",
  "HI - Hawaii",
  "IA - Iowa",
  "ID - Idaho",
  "IL - Illinois",
  "IN - Indiana",
  "KS - Kansas",
  "KY - Kentucky",
  "LA - Louisiana",
  "MA - Massachusetts",
  "MD - Maryland",
  "ME - Maine",
  "MI - Michigan",
  "MN - Minnesota",
  "MO - Missouri",
  "MS - Mississippi",
  "MT - Montana",
  "NC - North Carolina",
  "ND - North Dakota",
  "NE - Nebraska",
  "NH - New Hampshire",
  "NJ - New Jersey",
  "NM - New Mexico",
  "NV - Nevada",
  "NY - New York",
  "OH - Ohio",
  "OK - Oklahoma",
  "OR - Oregon",
  "PA - Pennsylvania",
  "PR - Puerto Rico",
  "RI - Rhode Island",
  "SC - South Carolina",
  "SD - South Dakota",
  "TN - Tennessee",
  "TX - Texas",
  "UT - Utah",
  "VA - Virginia",
  "VI - Virgin Islands",
  "VT - Vermont",
  "WA - Washington",
  "WI - Wisconsin",
  "WV - West Virginia",
  "WY - Wyoming",
];

const AccountTypes = ["Artist Account", "Fan Account"];
const Months = [
  "January",
  "February",
  "March",
  "April",
  "May",
  "June",
  "July",
  "August",
  "September",
  "October",
  "November",
  "December",
];

const getYears = () => {
  let currentYear = new Date().getFullYear();
  let minYear = currentYear - 119;
  let years = [];
  for (let i = currentYear; i >= minYear; i--) {
    years.push(i);
  }
  return years;
};
const Years = getYears();

const Signup = () => {
  const [isArtist, setArtist] = useState(false);
  const [birthMonth, setBirthMonth] = useState(0);
  const [birthYear, setBirthYear] = useState(new Date().getFullYear());
  const [describesYou, setDescribesYou] = useState("");
  const [errorMessage, setErrorMessage] = useState('');

  let Days = [];
  const getDays = () => {
    let month = birthMonth + 1;
    let year = birthYear;
    let numOfDays = new Date(year, month, 0).getDate();
    let days = [];
    for (let i = 1; i <= numOfDays; i++) {
      days.push(i);
    }
    Days = days;
  };

  return (
      <div className="signup-page">
        <Row>
          <Col xs={24} sm={24} md={14} lg={14}>
            <div className="signup-container">
              <div className="signup-bg" style={{ backgroundImage: `url(${BackgroundImage})` }}>
                <div className="signup-inner">
                  <div className="signup-form">
                    <div className="logo">
                      <a href="#">
                        <img src={LogoImage} alt="Versenation Logo" />
                      </a>
                    </div>
                    <Alert
                        style={{ display: errorMessage !== '' ? '' : 'none' }}
                        message={errorMessage}
                        type="error"
                        showIcon
                        closable
                    />
                    <Formik
                        initialValues={{
                          firstName: "",
                          lastName: "",
                          birthMonth: "",
                          birthDate: "",
                          birthYear: "",
                          email: "",
                          password: "",
                          passwordAgain: "",
                          city: "",
                          state: "",
                          zip: "",
                          accountType: "",
                          bio: "",
                          artistDescription: "",
                          descriptions: "",
                          genre: "",
                        }}
                        validationSchema={SignupSchema}
                        onSubmit={(signupData) => {

                          console.log(signupData);
                            let requestData ={
                                'name':signupData.firstName+" "+signupData.lastName,
                                'username': signupData.email,
                                'email': signupData.email,
                                'password': signupData.password,
                            }
                          if(isArtist) {
                              requestData.bio = signupData.bio;
                              requestData.artistDescription = signupData.artistDescription.toUpperCase();
                              console.log(requestData);
                            axios.post(URI.authentication.artistSignupURL(), requestData)
                                .then(response => {
                                  console.log(response);
                                })
                                .catch(err => {
                                  console.log(err);
                                  setErrorMessage("Oops! Something went wrong.");
                                });
                          } else {
                              console.log(requestData);
                            axios.post(URI.authentication.fanSignupURL(), requestData)
                                .then(response => {
                                  console.log(response);
                                })
                                .catch(err => {
                                  console.log(err);
                                  setErrorMessage("Oops! Something went wrong.");
                                });
                          }
                        }}
                    >
                      {({ errors, touched, handleBlur }) => (
                        <div>
                          <Form>
                            <Row gutter={[20, 0]}>
                              <Col xs={24} sm={24} lg={24} xl={12}>
                                <Input className="vn-txt" name="firstName" placeholder={"First Name"} />
                                {errors.firstName && touched.firstName ? (<div className="error">{errors.firstName}</div>) : null}
                              </Col>
                              <Col xs={24} sm={24} lg={24} xl={12}>
                                <Input className="vn-txt" name="lastName" placeholder={"Last Name"}/>
                                {errors.lastName && touched.lastName ? (<div className="error">{errors.lastName}</div>) : null}
                              </Col>
                            </Row>
                            <Row gutter={[20, 0]}>
                              <Col xs={24} sm={24} lg={24} xl={12}>
                              <div className="select-wrapper">
                                <Row gutter={[8, 0]}>
                                  <Col xs={12}>
                                    <Select listHeight={250} className="vn-select" name="birthMonth" placeholder={"Birthday (Month)"} onChange={(option) => setBirthMonth(option)} >
                                      {Months.map((state, index) => (
                                        <Select.Option value={index}>{state}</Select.Option>
                                      ))}
                                    </Select>
                                    {errors.birthMonth && touched.birthMonth ? (<div className="error-date">{errors.birthMonth}</div>) : null}
                                  </Col>
                                  <Col xs={5}>
                                    <Select className="vn-select" name="birthDate" placeholder={"(DD)"} onClick={getDays()}>
                                      {Days.map((day) => (
                                        <Select.Option value={day}>{day}</Select.Option>
                                      ))}
                                    </Select>
                                    {errors.birthDate && touched.birthDate ? (<div className="error-date">{errors.birthDate}</div>) : null}
                                  </Col>
                                  <Col xs={7}>
                                    <Select className="vn-select" name="birthYear" placeholder={"(Year)"} onChange={(option) => setBirthYear(option)} >
                                      {Years.map((year) => (
                                        <Select.Option value={year}>{year}</Select.Option>
                                      ))}
                                    </Select>
                                    {errors.birthYear && touched.birthYear ? (<div className="error-date">{errors.birthYear}</div>) : null}
                                  </Col>
                                </Row>
                                </div>
                              </Col>
                              <Col xs={24} sm={24} lg={24} xl={12}>
                                <Input className="vn-txt" name="email" type="email" placeholder={"E-mail"} />
                                {errors.email && touched.email ? <div className="error">{errors.email}</div> : null}
                              </Col>
                            </Row>
                            <Row gutter={[20, 0]}>
                              <Col xs={24} sm={24} lg={24} xl={12}>
                                <Input.Password className="vn-txt" name="password" placeholder={"Password"} />
                                {errors.password && touched.password ? <div className="error">{errors.password}</div> : null}
                              </Col>
                              <Col xs={24} sm={24} lg={24} xl={12}>
                                <Input.Password className="vn-txt" name="passwordAgain" placeholder={"Password Again"} />
                                {errors.passwordAgain && touched.passwordAgain ? <div className="error">{errors.passwordAgain}</div> : null}
                              </Col>
                            </Row>
                            <Row gutter={[20, 0]}>
                              <Col xs={24} sm={24} lg={24} xl={12}>
                                <Input className="vn-txt" name="city" placeholder={"City"}/>
                                {errors.city && touched.city ? <div className="error">{errors.city}</div> : null}
                              </Col>
                              <Col xs={24} sm={24} lg={24} xl={12}>
                              <div className="select-wrapper">
                                <div className="vn-search-select">
                                  <Select name="state" placeholder={"State"} showSearch={true}>
                                    {States.map((state) => (
                                        <Select.Option key={state} value={state}>
                                          {state}
                                        </Select.Option>
                                    ))}
                                  </Select>
                                </div>
                                {errors.state && touched.state ? <div className="error">{errors.state}</div> : null}
                              </div>
                              </Col>
                            </Row>
                            <Row gutter={[20, 0]}>
                              <Col xs={24} sm={24} lg={24} xl={12}>
                                <Input className="vn-txt" name="zip" placeholder={"ZIP"} />
                                {errors.zip && touched.zip ? <div className="error">{errors.zip}</div> : null}
                              </Col>
                              <Col xs={24} sm={24} lg={24} xl={12}>  
                                <div className="select-wrapper">
                                  <Select name="accountType" placeholder={"Account Type"}
                                          onChange={(option) => {option === "Artist Account" ? setArtist(true) : setArtist(false);}} >
                                    {AccountTypes.map((accountType) => (
                                        <Select.Option value={accountType}>{accountType}</Select.Option>
                                    ))}
                                  </Select>  
                                {errors.accountType && touched.accountType ? (<div className="error">{errors.accountType}</div>) : null}
                              </div>
                              </Col>
                            </Row>
                            {isArtist &&
                            <div>
                              <Row>
                                <Col xs={24} sm={24} lg={24} xl={24}>
                                  <div className="vn-txtarea">
                                    <Input.TextArea className="vn-text-area" name="bio" placeholder={"Bio"} />
                                    {errors.bio && touched.bio ? (<div className="error">{errors.bio}</div>) : null}
                                  </div>
                                </Col>
                              </Row>
                              <Row gutter={[20, 0]}>
                                <Col xs={24} sm={24} lg={24} xl={12}>
                                  <Input name="descriptions" placeholder={"Describes You"} value={describesYou} />
                                  {errors.artistDescription && touched.artistDescription ? (<div className="error">{errors.artistDescription}</div>) : null}
                                </Col>
                                <Col xs={24} sm={24} lg={24} xl={12}>
                                  <div className="describe-option">
                                  <Radio.Group name="artistDescription" options={ArtistDescriptions} optionType={"button"}
                                               onChange={(e) => setDescribesYou(e.target.value)} />
                                  </div>
                                </Col>
                              </Row>
                              <Row>
                                <Col xs={24} sm={24} lg={24} xl={24}>
                                  <div className="genere-select">
                                      <label>Genre Range</label>
                                      <Checkbox.Group name="genre" options={GenreRange} />
                                      {errors.genre && touched.genre ? (<div className="error">{errors.genre}</div>) : null}
                                    </div>
                                </Col>
                              </Row>
                            </div>
                            }
                            <br/>
                            <Row gutter={[20, 0]}>
                              <Col xs={24} lg={12}>
                              <button className="form-submt-btn btn" type="submit">Submit</button>
                              </Col>
                              <Col xs={24} lg={12}>
                              <ResetButton className="form-clear-btn btn">Clear</ResetButton>
                              </Col>
                            </Row>                              

                          </Form>
                              <div className="social-login">
                                <p>Continue with</p>
                                <ul>
                                  <li>
                                    <a href="#" className="btn fb-btn">
                                      <span>
                                        <img src={FacebookImage} alt="Facebook Logo" />
                                      </span>{" "}
                                      Facebook
                                    </a>
                                  </li>
                                  <li>
                                    <a href="#" className="btn ggl-btn">
                                      <span>
                                        <img src={GoogleImage} alt="Google Logo" />
                                      </span>
                                      Google
                                    </a>
                                  </li>
                                </ul>
                                <p>
                                  <span className="dots">
                                    <EllipsisOutlined />
                                  </span>
                                </p>
                              </div>
                          </div>
                      )}
                    </Formik>
                    
                  </div>
                </div>
              </div>
            </div>

          </Col>
          
          <Col xs={24} sm={24} md={10} lg={10}>
                <div className="login-description">
                  <div className="login-description-wrapper">
                    <h1>
                      It is time to<br />
                      Sign up with
                      <br />
                      <span>
                        Verse Nation
                        <br />
                        Marketplace
                      </span>
                    </h1>
                  </div>
                </div>
              </Col>
        </Row>
      </div>
    );
  };
  export default Signup;
