import React from "react";
import { Link } from "react-router-dom";
import { Menu, Layout, Row, Col, } from "antd";
import { useSelector, useDispatch } from 'react-redux';
import * as actionTypes from '../../Store/Actions/actionTypes';
import onLogout from '../Logout/Logout'; 
import "./NavigationBar.scss";

import LogoImage from "../../Resources/Images/logo.png";

const NavigationBar = () => {
  const dispatch = useDispatch();
  const afterLogout = () => dispatch({type: actionTypes.AUTH_LOGOUT});
  const isAuthenticated = useSelector(state => state.auth.isAuthenticated);
  let loginLink = null;
  let logoutLink = null;
 
  if(isAuthenticated) {
    logoutLink = <Menu.Item key="logout">
                  <div class="vn-main-btn">Logout <Link to="/logout" onClick={() => onLogout(afterLogout)} /></div>
                </Menu.Item>;
  } else {
    loginLink = <Menu.Item key="login">
                  Login <Link to="/login" />
                </Menu.Item>;
  } 
  
  const { Header } = Layout;
  return (
    <div>
      <Header class="header">
      
    
      <Row>
        <Col xs={6} sm={24} md={14} lg={6}>
          <div class="nav-logo">
            <a href="#">
              <img src={LogoImage} alt="Versenation Logo" />
            </a>
          </div>
        </Col>
        <Col xs={18} sm={24} md={14} lg={12}>
          <div class="main-menu">
            <Menu mode="horizontal">
              <Menu.Item key="home">  
                <div class="nav-link">Home</div>
                <Link to="/" />
              </Menu.Item>
              <Menu.Item key="streaming">
              <div class="nav-link">Streaming</div>
                <Link to="/streaming" />
              </Menu.Item>
              <Menu.Item key="help">
              <div class="nav-link">Help</div>
                <Link to="/help" />
              </Menu.Item>
              <Menu.Item key="about">
              <div class="nav-link">About</div>
                <Link to="/about" />
              </Menu.Item>
              <Menu.Item key="signup">
                <div class="nav-link">Signup</div>
                <Link to="/signup" />
              </Menu.Item>
          </Menu>
        </div>
      </Col>
        <Col xs={24} sm={24} md={14} lg={6}>
          <div className="main-menu log-menu">
          <Menu mode="horizontal">         
            {loginLink}
            {logoutLink}
        </Menu>
        </div>
        </Col>
      </Row>
      </Header>
    </div>
    
  );
};

export default NavigationBar;
